import * as React from "react";
import {Table, TableBody, TableCell, TableContainer, TablePagination, TableRow} from "@material-ui/core";
import './ArrayField.css'

interface Props {
    field?: any
    json?: any
    name?: string
    onChange?: any
    onClick?: any
}

const ArrayField: React.FC<Props> = ({field, json = [], name, onChange, onClick}) => {
    const [page, setPage] = React.useState(0);
    const [rowsPerPage, setRowsPerPage] = React.useState(5);

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(parseInt(event.target.value, 10));
        setPage(0);
    };

    // const handleClickAddItem = () => {
    //     const newArray = [...json]
    //     if (field.innerType.type === 'string') {
    //         newArray.push('')
    //     } else {
    //         const newObj = createObject(field.innerType)
    //         newArray.push(newObj)
    //     }
    //     onChange(name, newArray)
    // }
    //
    // const handleClickRemoveItem = (index) => {
    //     const newArray = json.filter((item, i) => i !== index)
    //     onChange(name, newArray)
    // }

    const handleClickItem = (item, i) => {
        onClick(field.innerType, item, onChange, name + (i + 1), onClick)
    }

    const handleClickArray = () => {
        onClick(field, json, onChange, name, onClick)
    }

    return (
        <div className='array-edit-container'>
            <div className='title'>
                <div onClick={handleClickArray}>{name}</div>
            </div>
            <div>
                <TableContainer>
                    <Table>
                        <TableBody>
                            {json
                                .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                                .map((item, index) => (
                                        <TableRow key={index}>
                                            <TableCell align="left" padding='none'>
                                                {name + ' '+ (index + page * rowsPerPage + 1)}
                                            </TableCell>
                                            <TableCell align="right">
                                                <div className='buttons'>
                                                    <button
                                                        onClick={() =>
                                                            handleClickItem(item, index + page * rowsPerPage)}
                                                    >
                                                        Edit
                                                    </button>
                                                </div>
                                            </TableCell>
                                        </TableRow>
                                    )
                                )}
                        </TableBody>
                    </Table>
                </TableContainer>
                {json.length > 5 &&
                <TablePagination
                    rowsPerPageOptions={[5]}
                    component="div"
                    count={json.length}
                    rowsPerPage={rowsPerPage}
                    page={page}
                    onChangePage={handleChangePage}
                    onChangeRowsPerPage={handleChangeRowsPerPage}
                />
                }
            </div>
        </div>
    )
}

export default ArrayField
